@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Comfortaa', cursive;
  color: #61DAFB;
  user-select:none;
}
/* body{
  
} */
.App {
  background-color: #282c34;
  text-align: center;
  padding: 0 3vw;
  height: calc(100vh - 50px);
}

@media(min-width:400px){
  .App {
    background-color: #282c34;
    text-align: center;
    padding: 0 5px;
    height: calc(100vh - 50px);
  }
}
.timerNumbersWrap{
  /* border: 1px solid rgba(97, 218, 251, 0.1); */

}
.timerNumbers{
  width: fit-content;
  font-size: 15px;
  padding: 50px 0 30px;
  margin: 0 auto ;
  display: grid;
  grid-template-columns: 20px 12px 20px 12px 20px 12px 20px;
  /* grid-template-columns: 5fr 3fr 5fr 3fr 5fr 3fr 5fr; */
}
@media(min-width:190px){
  .timerNumbers{
    grid-template-columns: 30px 18px 30px 18px 30px 18px 30px;
    /* grid-template-columns: 40px 24px 40px 24px 40px 24px 40px; */
    font-size: 25px;
  }
}
@media(min-width:260px){
  .timerNumbers{
    grid-template-columns: 40px 24px 40px 24px 40px 24px 40px;
    font-size: 35px;
  }
}
@media(min-width:350px){
  .timerNumbers{
    grid-template-columns: 50px 30px 50px 30px 50px 30px 50px;
    font-size: 45px;
  }
}
.timerButtons{
  max-width: 400px;
  margin: 0 auto;
  display: grid;
  /* grid-template-columns: repeat(3,1fr); */
  margin-bottom: 50px;
  grid-template-columns: repeat(5,1fr);
}
.timerButtons__vacuum{
  max-width: 400px;
  margin: 0 auto;
  display: grid;
  /* grid-template-columns: repeat(3,1fr); */
  margin-bottom: 50px;
  grid-template-columns: repeat(2,1fr);
}

button{
  cursor: pointer;
  color: black;
  padding: 3vw;
  font-size: 4vw;
}
@media(min-width:400px){
  button{
    padding: 15px;
    font-size: 15px;
  }
}
.loops{
  max-height: 600px;
  overflow-y: scroll;
  scrollbar-width: none;
}
.loops::-webkit-scrollbar {
  display: none;
}
.loop{
  max-width: 190px;
  position: relative;
  display: grid;
  margin: 0px auto;
  padding: 10px 0;
  grid-template-columns: 40px 1fr;
  
  border-bottom: 1px solid #61DAFB;
  /* margin: 5px; */
}
.loop:last-child{
  border-bottom: none;

}
.loopId{
  font-weight: bold;
}
.timerNumbers__item__number{
  color: inherit;
}
.afterBeforeNumber{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -2vw;
  color: inherit;
}

@media(min-width:400px){
  .afterBeforeNumber{
    margin-top: -7px;
  }
}
.customTimer{
  margin: 0 auto;
  max-width: 350px;
  display: grid;
  grid-template-columns: 3fr 2fr 3fr 2fr 3fr 2fr;
  align-self: center;
  border: 1px solid rgba(97, 218, 251, 0.1);
  padding: 2vw;
}
.customTimer input{
  text-align: center;
  width: 100%;
  color: inherit;
  font-size: 10vw;
  background: none;
  border: none;
  outline: none;
}
.customTimer p{
  display: flex;
  align-self: end;
  width: fit-content;
  margin: 0 auto;
  font-size: 5vw;
  background: none;
  border: none;
  color: inherit;
  outline: none;
}
.customTimerButton{
  margin-top: 20px;
  max-width: 200px;
  width: 70%;
}
.stopButton{
  max-width: 200px;
  width: 70%;
}
@media(min-width:400px){
  .customTimer{
    margin: 0 auto;
    max-width: 350px;
    display: grid;
    grid-template-columns: 3fr 2fr 3fr 2fr 3fr 2fr;
    /* font-size: 20px; */
    align-self: center;
    border: 1px solid rgba(97, 218, 251, 0.1);
    padding: 10px;
  }
  .customTimer input{
    text-align: center;
    width: 100%;
    color: inherit;
    font-size: 40px;
    background: none;
    border: none;
    outline: none;
  }
  .customTimer p{
    display: flex;
    align-self: end;
    width: fit-content;
    margin: 0 auto;
    font-size: 18px;
    background: none;
    border: none;
    color: inherit;
    outline: none;
  }
  .customTimerButton{
    margin-top: 20px;
    width: 200px;
    padding: 10px;
    /* height: 40px; */
  }
  .stopButton{
    width: 200px;
    /* width: 70%; */
  }
}


.invisibleBlockTimer{
  height: 100px;
  width: 100vw;
  position: absolute;
  background:rgba(0, 0, 0, 0.541);
  z-index: 99;
  top: -50px;
}

.navbarWrap{
  height: 50px;
  background: rgb(32, 35, 43);
  display: flex;
  align-items: center;
  position: relative;
}
.navbar{
  /* position: relative; */
  /* align-self: center; */
}
.navbar button{
  background: none;
  border: none;
  cursor: pointer;
  color: #61DAFB;
}
.navbar button:hover{
  opacity: 0.7;
}

.vacuum__inputs{
  margin: 0 auto;
  max-width: 350px;
  display: grid;
  /* grid-template-columns: 1fr 2fr 1fr 2fr 1fr 2fr; */
  grid-template-columns: 1fr 1fr 1fr;
}
.vacuum__inputs input{
  padding: 15px;
  width: 80%;
  color: #61DAFB;
  font-size: 35px;
  text-align: center;
  background: rgba(97, 218, 251, 0.2);
  margin: 0 auto;
  margin-bottom: 20px;
  border-radius: 5px;
  /* width: fi; */
  border: none;
  outline: none;
}

.App__vacuum {
  background-color: #282c34;
  text-align: center;
  padding: 0 3vw;
  height: calc(100vh - 50px);
}